export default "const scrollContainerBlock = document.querySelectorAll('.demo_scroll-container-block > *')\n\nif (scrollContainerBlock.length) {\n  setTimeout(function() {\n    // after 2 seconds\n    for (const el of scrollContainerBlock) {\n      el.classList.add('scroll-block')\n    }\n    alert('scroll blocked')\n  }, 2000)\n}\n"